.reviews {
  padding-top: 200px;
  padding-bottom: 200px;

  @include tablet {
    padding-top: 100px;
    padding-bottom: 100px;
  }

  &__container {
    display: flex;
    align-items: center;

    @include tablet {
      flex-direction: column;
    }
  }

  &__heading {
    flex: 0 0 50%;

    @include tablet {
      flex: 1 1 100%;
      margin-bottom: 70px;
    }
  }

  &__title {
    max-width: 472px;
    margin: 0;
    margin-bottom: 24px;

    @include tablet {
      max-width: 100%;
      margin-bottom: 15px;
      text-align: center;
    }
  }

  &__descr {
    @include tablet {
      text-align: center;
    }
  }

  &__swiper {
    flex: 0 1 50%;
    position: relative;
  }

  &-pagination {
    max-width: 442px;
    margin: 0 auto;
    bottom: 0;

    @include tablet {
      text-align: center;
    }
  }

  &__btns {
    @include mobile {
      display: none;
    }
  }

  &__slider-btn {
    z-index: 1000;

    &--prev {
      position: absolute;
      content: "";
      width: 36px;
      height: 36px;
      background-image: url("../img/prev.svg");
      left: 0;
      top: 50%;
      margin-top: calc(0px - (var(--swiper-navigation-size)/ 2));
      background-size: cover;
      background-repeat: no-repeat;
    }

    &--next {
      position: absolute;
      content: "";
      width: 36px;
      height: 36px;
      background-image: url("../img/next.svg");
      right: 0;
      top: 50%;
      margin-top: calc(0px - (var(--swiper-navigation-size)/ 2));
      background-size: cover;
      background-repeat: no-repeat;
    }
  }
}

.slide {
  max-width: 442px;
  margin: 0 auto;
  padding-bottom: 50px;

  @include tablet {
    max-width: 600px;
  }

  @include small-tablet {
    max-width: 450px;
  }

  &::before {
    position: absolute;
    content: "";
    width: 40px;
    height: 29px;
    background-image: url("../img/reviews/mark.svg");
    right: 60px;
    top: 0;
    background-size: cover;
    background-repeat: no-repeat;
  }
}

.review__item {
  &-stars {
    margin-bottom: 15px;

    @include tablet {
      text-align: center;
    }
  }

  &-star:not(:last-child) {
    margin-right: 9px;
  }

  &-text {
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 16px;
    line-height: 200%;
    color: #90A3B4;

    @include tablet {
      text-align: center;
    }
  }

  &-human {
    display: flex;
    align-items: center;

    @include tablet {
      flex-direction: column;
      margin-top: 10px;
    }
  }

  &-photo {
    margin-right: 15px;

    @include tablet {
      margin-right: 0;
      margin-bottom: 10px;
    }
  }

  &-name {
    margin: 0;
    margin-bottom: 4px;
    font-weight: 700;
    font-size: 20px;
    line-height: 120%x;
    letter-spacing: 0.2px;
    color: var(--darkBlue-color);

    @include tablet {
      text-align: center;
    }
  }

  &-post {
    font-weight: 400;
    font-size: 16px;
    line-height: 175%;
    color: var(--grey-color);

    @include tablet {
      text-align: center;
    }
  }
}

.swiper-pagination-bullet {
  width: 10px !important;
  height: 10px !important;
  background-color: #E2ECF4 !important;
  opacity: 1 !important;
}

.swiper-pagination-bullet:not(:last-child) {
  margin-right: 8px !important;
}

.swiper-pagination-bullet-active {
  width: 10px !important;
  height: 10px !important;
  background-color: var(--darkBlue-color) !important;
}

.swiper__wrapper {
  @include small-tablet {
    width: 70% !important;
  }
}
