.partners {
  padding-top: 130px;
  padding-bottom: 100px;

  @include small-tablet {
    padding-top: 110px;
    padding-bottom: 20px;
  }

  @media (max-width: 700px) {
    padding-top: 70px;
  }

  @media (max-width: 530px) {
    padding-top: 50px;
  }



  &__logos-list {
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
  }
}

.logo__item {
  padding: 15px 15px;

  @include small-tablet {
    padding: 10px 5px;
  }

  svg {
    min-width: 150px;

    @include small-tablet {
      min-width: 100px;
    }

    @include mobile {
      min-width: 90px;
    }
  }
}
