.hero {
  position: relative;
  height: 860px;
  background: linear-gradient(216.04deg, #27C0EF 13.84%, #00A8DB 83.53%);

  @include desktop {
    height: 830px;
  }

  @include tablet {
    height: 1120px;
  }

  @include medium-tablet {
    height: 1050px;
  }

  @include small-tablet {
    height: 950px;
  }

  @include mobile {
    height: 800px;
  }

  @media (max-width: 450px) {
    height: 700px;
  }

  &__content {
    max-width: 620px;
    padding-top: 247px;

    @include desktop {
      padding-top: 200px;
    }

    @include tablet {
      max-width: 100%;
    }

    @include mobile {
      padding-top: 150px;
    }

    @media (max-width: 450px) {
      padding-top: 120px;
    }

  }

  &__title {
    position: relative;
    margin: 0;
    margin-bottom: 45px;
    max-width: 620px;
    font-weight: 700;
    font-size: 64px;
    line-height: 119%;
    color: var(--light-color);

    @include tablet {
      max-width: 100%;
    }

    @include small-tablet {
      font-size: 56px;
      margin-bottom: 35px;
    }

    @include mobile {
      font-size: 36px;
      margin-bottom: 20px;
    }

    &::before {
      position: absolute;
      content: "";
      width: 180px;
      height: 51px;
      background-image: url(../img/hero/decor.svg);
      background-repeat: no-repeat;
      left: 5px;
      bottom: -25px;

      @include tablet {
        left: 290px;
      }

      @include small-tablet {
        display: none;
      }
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 32px;
    max-width: 460px;
    font-weight: 400;
    font-size: 16px;
    line-height: 162%;
    color: var(--light-color);

    @include tablet {
      max-width: 600px;
    }

    @include mobile {
      margin-bottom: 25px;
    }
  }

  &__btns {
    display: flex;
    gap: 42px;
    align-items: center;

    @media (max-width: 450px) {
      flex-direction: column;
      flex-wrap: wrap;
      align-items: center;
      gap: 20px;
      text-align: center;
    }
  }

  &__link {
    color: var(--light-color);
    z-index: 50;

    @media (max-width: 450px) {
      max-width: 100%;

    }
  }

  &__img {
    position: absolute;
    top: 156px;
    right: -373px;
    z-index: 0;

    @include desktop {
      top: 250px;
      right: -550px;
    }

    @include tablet {
      top: 570px;
      left: 0;
      right: 20px;
    }

    @include mobile {
      top: 490px;
    }

    @media (max-width: 395px) {
      top: 530px;
    }


    img {
      width: 100%;
      object-fit: contain;
      z-index: 0;

      @include desktop {
        width: 80%;
      }

      @include tablet {
        width: 110%;
      }

    }
  }
}

.btn {
  display: inline-flex;
  padding: 18px 35px;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  text-align: center;
  color: #00A8DB;
  background: #FFFFFF;
  border-radius: 4px;
  z-index: 50;
  transition: box-shadow 0.3s ease-in-out;

  @media (max-width: 450px) {
    width: 100%;
    align-items: center;
    justify-content: center;
  }

  &:hover {
    box-shadow: 0px 4px 15px rgba(0, 84, 144, 0.25);
  }

  &:active {
    box-shadow: 0px 4px 5px rgba(0, 84, 144, 0.6);
  }
}


