.features {
  padding-top: 80px;
  padding-bottom: 80px;
  background-color: var(--lightGrey-color);

  @include tablet {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  @include tablet {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  &__title {
    margin: 0;
    margin-bottom: 24px;
    text-align: center;

    @include tablet {
      margin-bottom: 15px;
    }
  }

  &__descr {
    max-width: 580px;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 76px;

    @include tablet {
      margin-bottom: 40px;
    }
  }

  &__cards {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 42px;

    @include tablet {
      grid-template-columns: repeat(2, 1fr);
      column-gap: 30px;
      row-gap: 25px;
    }
  }

  &__item {
    height: 300px;
    padding: 30px;
    background-color: var(--light-color);
    border-radius: 8px;
    transition: box-shadow 0.3s ease-in-out;

    @include tablet {
      height: auto;
      padding: 25px;
    }

    &:hover {
      box-shadow: 0px 25px 55px rgba(90, 113, 132, 0.15);
    }

    &-img {
      width: 56px;
      height: 56px;
      margin-bottom: 26px;
    }

    &-title {
      margin: 0;
      margin-bottom: 16px;
      font-weight: 700;
      font-size: 20px;
      line-height: 160%;
      letter-spacing: 0.2px;
      color: var(--darkBlue-color);
    }

    &-descr {
      font-weight: 400;
      font-size: 15px;
      line-height: 160%;
      letter-spacing: 0.3px;
      color: #8EA9C1;
    }
  }
}

