.about {
  padding-top: 105px;
  padding-bottom: 125px;

  @include tablet {
    padding-top: 20px;
    padding-bottom: 70px;
  }

  @include small-tablet {
    padding-top: 20px;
    padding-bottom: 30px;
  }

  &__column {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include mobile {
      flex-direction: column;
    }
  }

  &__column-content {

    @include mobile {
      order: 1;
      margin-bottom: 35px;
    }


  }

  &__column-img {
    position: relative;

    @include mobile {
      order: 2;
    }

    &::before {
      position: absolute;
      content: "";
      width: 600px;
      height: 700px;
      background-image: url(../img/about/decor-arrow.svg);
      background-repeat: no-repeat;
      top: 70px;
      left: 70px;
      z-index: -1;

      @include tablet {
        display: none;
      }

    }

    img {
      width: 100%;
      height: 100%;

      @include tablet {
        width: 80%;
        height: 80%;
      }

      @include mobile {
        width: 50%;
        height: 50%;
      }
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 24px;
    max-width: 390px;

    @include mobile {
      max-width: 100%;
      margin-bottom: 15px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 36px;
    max-width: 472px;

    @include mobile {
      max-width: 100%;
      margin-bottom: 20px;
    }
  }
}
