.cta {
  margin-bottom: 176px;

  @include tablet {
    margin-bottom: 100px;
  }

  @include medium-tablet {
    margin-bottom: 75px;
  }

  @include mobile {
    margin-bottom: 40px;
  }

  &__block {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 65px 90px 65px 70px;
    background: linear-gradient(270.04deg, #01B8D9 0.05%, #24CEED 97.84%);
    border-radius: 8px;

    @include medium-tablet {
      padding: 55px 70px 55px 60px;
    }

    @include small-tablet {
      padding: 40px 35px 40px 35px;
      flex-direction: column;
      align-items: start;
    }

    @include mobile {
      padding: 25px 20px 25px 20px;
    }



    &::before {
      position: absolute;
      content: "";
      top: 0;
      right: 0;
      width: 420px;
      height: 100%;
      background-image: url('../img/cta/decor.svg');
      background-repeat: no-repeat;

      @include small-tablet {
        display: none;
      }
    }
  }

  &__block-content {
    @include small-tablet {
      margin-bottom: 25px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    max-width: 355px;
    line-height: 122%;
    letter-spacing: 0.3px;
    color: var(--light-color);

    @include mobile {
      font-size: 26px;
      margin-bottom: 5px;
    }
  }

  &__descr {
    font-weight: 500;
    font-size: 14px;
    line-height: 229%;
    letter-spacing: 0.3px;
    color: var(--light-color);
  }

  &__people {
    display: flex;
    align-items: center;

    &-img:not(:last-child) {
      margin-right: -12px;
      transition: margin-right 0.3s ease-in-out;

      &:hover {
        margin-right: 0;
      }
    }

    &-text {
      margin-left: 27px;
      font-weight: 500;
      font-size: 14px;
      line-height: 229%;
      letter-spacing: 0.3px;
      text-decoration: underline;
      color: var(--light-color);
      transition: text-decoration 0.3s ease;

      &:hover {
        text-decoration: none;
      }
    }
  }
}
