.post {
  padding-top: 125px;
  padding-bottom: 170px;

  @include tablet {
    padding-top: 40px;
    padding-bottom: 80px;
  }

  @include mobile {
    padding-top: 40px;
    padding-bottom: 50px;
  }

  &__column {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include mobile {
      flex-direction: column;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 24px;
    max-width: 450px;

    @include medium-tablet {
      max-width: 400px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 24px;
    max-width: 500px;

    @media (max-width: 1140px) {
      max-width: 400px;
    }

    @include medium-tablet {
      max-width: 350px;
    }
  }

  &__column-img {
    position: absolute;
    right: -50px;
    top: 20px;

    @include tablet {
      top: 70px;
      right: -150px;
    }

    @include medium-tablet {
      top: 100px;
      right: -350px;
    }

    @include small-tablet {
      top: 160px;
      right: -450px;
    }

    @include mobile {
      display: none;
    }

    img {
      width: 100%;
      height: 100%;

      @include tablet {
        width: 80%;
        height: 80%;
      }

      @include medium-tablet {
        width: 70%;
        height: 70%;
      }

      @include small-tablet {
        width: 50%;
        height: 50%;
      }
    }
  }

}

.check__list {
  margin-bottom: 33px;
}

.check__item {
  display: flex;
  align-items: center;

  &:not(:last-child) {
    margin-bottom: 17px;

    @include tablet {
      margin-bottom: 12px;
    }
  }

  &-icon {
    margin-right: 12px;

    @include tablet {
      margin-right: 9px;
    }
  }

  &-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 21px;
    letter-spacing: 0.3px;
    color: var(--grey-color);
  }
}
