.title {
  font-weight: 700;
  font-size: 36px;
  line-height: 144%;
  letter-spacing: 0.3px;
  color: #034D82;

  @include small-tablet {
    font-size: 28px;
  }

  @include mobile {
    font-size: 24px;
  }
}

.descr {
  font-weight: 400;
  font-size: 16px;
  line-height: 200%;
  letter-spacing: 0.3px;
  color: var(--grey-color);

  @include small-tablet {
    line-height: 140%;
  }

  @include mobile {
    font-size: 14px;
    line-height: 120%;
  }
}
