.footer {
  padding-top: 90px;
  padding-bottom: 75px;
  background-color: #081315;

  @include tablet {
    padding-top: 70px;
    padding-bottom: 60px;
  }

  @media (max-width: 680px) {
    padding-top: 50px;
    padding-bottom: 40px;
  }

  &__row {
    display: flex;

    @include tablet {
      flex-direction: column;
    }

    @media (max-width: 680px) {
      align-items: center;
    }
  }

  &__info {
    margin-right: 30px;

    @include tablet {
      flex: 1 1 100%;
      margin-bottom: 60px;
      margin-right: 0;
    }

    @media (max-width: 680px) {
      margin-bottom: 10px;
      flex-direction: column;
      text-align: center;

    }
  }

  &__logo {
    margin-bottom: 28px;

    @include tablet {
      margin-bottom: 20px;
    }

    @media (max-width: 680px) {
      text-align: center;
    }
  }

  &__info-text {
    margin-bottom: 32px;
    max-width: 264px;
    font-weight: 400;
    font-size: 16px;
    line-height: 175%;
    color: #D3D6D8;

    @include tablet {
      max-width: 100%;
      margin-bottom: 20px;
    }
  }

  &__socials {
    display: flex;
    align-items: center;
    column-gap: 6px;

    @media (max-width: 680px) {
      justify-content: center;
      text-align: center;
    }

    svg rect{
      transition: fill 0.3s ease-in-out, opacity 0.3s ease-in-out;
    }

    svg rect:hover {
      opacity: 1;
      fill: var(--blue-color);
    }

  }

  &__nav {
    display: flex;

    @media (max-width: 680px) {
      display: none;
    }
  }

  &__nav-column {
    width: 165px;
  }

  &__nav-column:not(:last-child) {
    margin-right: 30px;
  }

  &__nav-title {
    margin: 0;
    margin-bottom: 33px;
    font-weight: 700;
    font-size: 18px;
    line-height: 23px;
    color: var(--light-color);
  }

  &__nav-item:not(:last-child) {
    margin-bottom: 25px;
  }

  &__nav-link {
    font-weight: 400;
    font-size: 16px;
    line-height: 21px;
    color: #D3D6D8;
    transition: color 0.3s ease-in-out;

    &:hover {
      color: var(--light-color);
    }
  }

  &__copy {
    margin-top: 96px;

    @include tablet {
      margin-top: 65px;
    }

    @include small-tablet {
      margin-top: 45px;
    }

    @media (max-width: 680px) {
      margin-top: 20px;
    }
  }

  &__copy-img {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 24px;
    text-align: center;

    @include small-tablet {
      margin-bottom: 15px;
      width: 50px;
      height: 50px;
    }
  }

  &__copy-text {
    font-weight: 700;
    font-size: 16px;
    line-height: 21px;
    text-align: center;
    color: var(--light-color);
  }
}
