:root {
  // base
  --font-family: "DMSans", sans-serif;
  --content-width: 1160px;
  --container-offset: 20px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --blue-color: #04AADD;
  --darkBlue-color: #034D82;
  --grey-color: #90A3B4;
  --lightGrey-color: #EFFBFF;
  --dark-color: #081315;
  --light-color: #fff;
}
