.header {
  position: absolute;
  left: 0;
  top: 0;
  padding: 40px 0;
  width: 100%;
  z-index: 100;

  @include mobile {
    padding: 30px 0;
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.nav {
  display: inline-flex;
  align-items: center;
  gap: 40px;

  @include small-tablet {
    position: fixed;
    display: flex;
    flex-direction: column;
    left: 0;
    top: 0;
    z-index: 500;
    overflow-y: auto;
    width: 100%;
    height: 100vh;
    background-color: var(--darkBlue-color);
    opacity: 0;
    visibility: visible;
    transition: opacity 0.3s ease, visibility 0.3s ease;
  }

  &__list {
    display: flex;
    align-items: center;

    @include small-tablet {
      margin-top: 150px;
      justify-content: center;
      flex-direction: column;
      align-items: center;
      width: 100%;
      height: 100%;
    }
  }

  &__item {
    position: relative;

    &:not(:last-child) {
      margin-right: 40px;

      @include small-tablet {
        margin-right: 0;
      }
    }

    @include small-tablet {
      margin-right: 0;
      margin-bottom: 70px;
    }
  }

  &__btn {
    border: none;
    padding: 8px 10px;
    gap: 5px;
    font-size: 14px;
    font-weight: 500;
    color: #fff;
    background-color: transparent;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;

    @include small-tablet {
      font-size: 20px;
      line-height: 30px;
    }

    &--active {
      background-color: var(--darkBlue-color);

      img {
        transform: rotate(180deg);
      }
    }
  }

  &__link {
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    text-align: center;
    letter-spacing: 0.3px;
    color: var(--light-color);
    transition: color 0.3s ease-in-out;

    @include small-tablet {
      font-size: 20px;
      line-height: 30px;
    }

    &:hover {
      color: var(--darkBlue-color);
    }
  }
}

  .dropdown {
    position: absolute;
    left: 0;
    top: 100%;
    width: 150px;
    background-color: var(--darkBlue-color);
    border-top-right-radius: 5px;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }

  .dropdown--active {
    opacity: 1;
    visibility: visible;
  }

  .dropdown__link {
    display: block;
    width: 100%;
    padding: 8px 10px;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.3px;
    color: var(--light-color);
  }

  .login__btn {
    padding: 10px 30px;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    text-align: center;
    color: #FFFFFF;
    background: linear-gradient(278.21deg, #005490 1.66%, #328BCB 96.78%);
    border-radius: 4px;
    transition: box-shadow 0.3s ease-in-out;

    @include small-tablet {
      margin-bottom: 50px;
    }

    &:hover {
      box-shadow: 0px 4px 15px rgba(0, 84, 144, 0.25);
    }

    &:active {
      box-shadow: 0px 4px 5px rgba(0, 84, 144, 0.6);
    }
}

.burger {
  @include burger;
  z-index: 1000;
  height: 20px;
  color: #fff;

  @media (min-width: 768px) {
    display: none;
  }
}

.menu--active {
  opacity: 1;
  visibility: visible;
}
